<template>
  <div class="course-list" v-loading="loading" element-loading-text="数据加载中...">
    <div v-if="courses.length">
      <div v-for="item in courses" :key="item.id" @click="handleClick(item.id)">
        <el-card :title="item.title">
          <div slot="header" class="card-header">
            <span>{{item.title}}</span>
            <span>{{item.lessonCount}}课时</span>
          </div>
          <div>播放时间: {{item.startDate}}至{{item.endDate}}</div>
          <div>回看时间: {{getPlaybackDate(item)}}</div>
        </el-card>
      </div>
    </div>
    <div class="none" v-else>
      没有课程
    </div>
  </div>
</template>

<script>

export default {
  data() {
    return {
      courses: [],
      loading: false,
    }
  },
  methods: {
    handleClick(id) {
      this.$router.push({ path: '/detail', query: { id } })
    },
    getPlaybackDate({ playbackMethod, playbackAbsoluteDate, playbackRelativeDays }) {
      if (playbackMethod === 1) return playbackAbsoluteDate
      if (playbackMethod === 2) return `播放${playbackRelativeDays}天后`
    },
  },
  mounted() {
    this.loading = true
    this.$api.getCourses({
      filterKind: 'mine',
    }).then(({ data: { courses } }) => {
      this.courses = courses
      this.loading = false
    })
  },
}
</script>

<style lang="less" scoped>
.course-list {
  min-height: ~"calc(100vh - 90px)";
  > div {
    width: inherit;
    display: flex;
    flex-wrap: wrap;
    > div {
      box-sizing: border-box;
      width: 25%;
      padding: 10px;
      &:nth-child(4n + 1) {
        padding-left: 0;
      }
      &:nth-child(4n + 4) {
        padding-right: 0;
      }
    }
    /deep/ .el-card {
      cursor: pointer;
    }
    /deep/ .card-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
  .none {
    font-size: 20px;
    font-weight: bold;
  }
}
</style>




