/* eslint-disable camelcase */
import axios from 'axios'
import qs from 'qs'

axios.interceptors.request.use((config) => {
  const { headers } = config
  const { localStorage: { accessToken = '{}' } } = window
  const token = JSON.parse(accessToken).access_token
  return { ...config, headers: Object.assign(headers, token ? { Authorization: `Bearer ${token}` } : {}) }
}, (error) => Promise.reject(error));

export default class Api {
  userLogin = ({
    client_id = '2d3d9191b47682118f24',
    client_secret = '589bfebc1dda118532673d315d563f3776d84cf8',
    refresh_token,
    grant_type,
    username,
    password,
  }) => axios.post('/pad/api/account/login', qs.stringify(Object.assign({
    client_id,
    client_secret,
  }, grant_type === 'password' ? {
    username,
    password,
    grant_type,
  } : {
    refresh_token,
    grant_type,
  })))

  getStudentInfo = () => axios.get('/banker/api/users/?fields=username')

  getCourses = (params) => axios.get('/pad/api/holiday-courses', { params })

  getCourseDetail = ({ id }) => axios.get(`/pad/api/holiday-courses/${id}`)

  getCourseSubjects = ({ id }) => axios.get(`/pad/api/holiday-courses/${id}/subjects`)

  getCourseDates = ({ id }) => axios.get(`/pad/api/holiday-courses/${id}/dates`)

  getLessons = (id, params) => axios.get(`/pad/api/holiday-courses/${id}/lessons`, { params })
}



