<template>
  <div class="login-container">
    <el-card>
      <el-form :model="form">
        <el-form-item style="margin: 35px 0">
          <img :src="require('assets/img/login/logo.png')" style="width: 150px" alt="沃课名校课程" />
        </el-form-item>
        <el-form-item>
          <el-input placeholder="用户名" prefix-icon="" v-model="form.username" />
        </el-form-item>
        <el-form-item>
          <el-input placeholder="密码" prefix-icon="" type="password" v-model="form.password" @keyup.native.enter="login" />
        </el-form-item>
        <el-form-item style="margin-top: 40px">
          <el-button type="primary" @click="login" :disabled="loginDisabled" :loading="loading">登录</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        username: '',
        password: '',
      },
      loading: false,
    }
  },
  methods: {
    login() {
      if (this.loginDisabled) return
      this.loading = true
      const { form: { username, password } } = this
      this.$api.userLogin({
        grant_type: 'password',
        username,
        password,
      }).then(({ data }) => {
        window.localStorage.setItem('accessToken', JSON.stringify({ ...data, login_time: new Date() }))
        this.$router.push('/list')
        this.loading = false
      }).catch(({ response: res }) => {
        this.form.password = ''
        this.$message({ type: 'error', message: res.data && res.data.error ? res.data.error : '登录失败' })
        this.loading = false
      })
    },
  },
  computed: {
    loginDisabled() {
      const { form: { username, password } } = this
      return !username.trim().length || !password.trim().length
    },
  },
}
</script>

<style lang="less">
#app {
  background: #eee;
}
</style>

<style lang="less" scoped>
.login-container {
  width: 420px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
  .title {
    font-size: 24px;
  }
  .el-form-item {
    width: 100%;
    text-align: center;
    /deep/ button {
      width: 100%;
    }
  }
}
</style>




