<template>
  <div class="course-detail" v-loading="loading">
    <strong class="course-name">{{title}}</strong>
    <el-card class="video-container" v-if="selectItem">
      <div class="title"></div>
      <div class="describe">
        <span>{{selectItem.title}}</span>
        <span>老师: {{selectItem.teacherName}}</span>
      </div>
      <video autoplay controls :src="selectItem.videoPath.cdn" controlslist="nodownload"></video>
    </el-card>
    <div class="course-list">
      <div class="course-list-header">
        <strong>课时列表</strong>
        <div>
          <el-select v-model="selectDate" size="medium" style="width: 150px">
            <el-option v-for="{label, value} in dates" :key="value" :value="value" :label="label"></el-option>
          </el-select>
          <el-select v-model="selectSubject" size="medium" style="width: 150px">
            <el-option v-for="{label, value} in subjects" :key="value" :value="value" :label="label"></el-option>
          </el-select>
        </div>
      </div>
      <div class="list">
        <div v-for="item in lessons" :key="item.id" @click="handleItemClick(item)">
          <el-card :style="{backgroundColor: item.id === selectId ? '#ddd' : '#fff'}">
            <div class="head" :style="{color: statusMap[item.status].color}">
              <span>{{statusMap[item.status].text}}</span>
            </div>
            <div :title="item.title">
              <div class="title">{{item.title}}</div>
              <div class="time">上课时间: {{item.startTime}}</div>
              <div>
                <span class="teacher">老师: {{item.teacherName}}</span>
                <span></span>
              </div>
            </div>
          </el-card>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import dayjs from 'dayjs'
import { SUBJECTS } from '@/assets/js/consts/common'

const statusMap = {
  0: { text: '上课中', color: '#F56C6C' },
  1: { text: '可回看', color: '#67C23A' },
  2: { text: '已结束', color: '#909399' },
  3: { text: '未开课', color: '#909399' },
}

export default {
  data() {
    return {
      statusMap,
      title: '',
      lessons: [],
      subjectIds: [],
      lessonDates: [],
      selectSubject: 0,
      selectDate: 0,
      selectId: null,
      loading: false,
    }
  },
  methods: {
    lessonsStatusJudge({ startTime, videoSeconds, playbackMethod, playbackAbsoluteDate, playbackRelativeDays }) {
      const now = dayjs()
      const isBeforeStartTime = now.isBefore(dayjs(startTime))
      const isSameStartTime = now.isSame(dayjs(startTime))
      const isAfterStartTime = now.isAfter(dayjs(startTime))
      const isBeforeEndTime = now.isBefore(dayjs(startTime).add(videoSeconds, 'second'))
      if (isBeforeStartTime) return 3
      if (isSameStartTime || (isAfterStartTime && isBeforeEndTime)) return 0
      if (playbackMethod === 1 && now.isAfter(dayjs(playbackAbsoluteDate))) return 1
      if (playbackMethod === 2 && now.isAfter(dayjs(startTime).add(playbackRelativeDays, 'day'))) return 1
      return 2
    },
    updateLessons(lessons) {
      this.lessons = lessons.map(ele => Object.assign(ele, { status: this.lessonsStatusJudge(ele) }))
    },
    getLessons(id, { date = 0, subjectId = 0 }) {
      this.$api.getLessons(id, { date, subjectId }).then(({ data }) => {
        if (data.lessons) {
          this.updateLessons(data.lessons)
        } else {
          this.$message({ type: 'error', message: data.detail })
        }
      })
      this.loading = false
    },
    getCourseDetail({ id }) {
      this.$api.getCourseDetail({ id }).then(({ data: { title } }) => {
        this.title = title
      })
    },
    handleItemClick({ id, status }) {
      if (status === 3) return this.$message({ type: 'error', message: '还未开课' })
      if (status === 2) return this.$message({ type: 'error', message: '还没到回看时间' })
      this.selectId = id
    },
    autoSelectInClass() {
      const findResult = this.lessons.find(ele => ele.status === 0)
      if (findResult && this.selectId === null) this.selectId = findResult.id
    },
  },
  async mounted() {
    this.loading = true
    const { id } = this
    this.getCourseDetail({ id })
    const { data: { subjectIds } } = await this.$api.getCourseSubjects({ id })
    const { data: { lessonDates } } = await this.$api.getCourseDates({ id })
    this.subjectIds = subjectIds
    this.lessonDates = lessonDates
    this.getLessons(id, {})
  },
  computed: {
    id() {
      return this.$route.query.id
    },
    subjects() {
      return [{ label: '全部学科', value: 0 }].concat(this.subjectIds.map(ele => ({ label: Object.assign(SUBJECTS, { 99: '综合' })[ele], value: ele })))
    },
    dates() {
      return [{ label: '全部日期', value: 0 }].concat(this.lessonDates.map(ele => ({ label: ele, value: ele })))
    },
    selectItem() {
      return this.lessons.find(ele => ele.id === this.selectId)
    },
  },
  watch: {
    selectSubject(val) {
      this.getLessons(this.id, { subjectId: val, date: this.selectDate })
    },
    selectDate(val) {
      this.getLessons(this.id, { subjectId: this.selectSubject, date: val })
    },
    lessons(val, oldVal) {
      if (!oldVal.length && val.length) {
        this.autoSelectInClass()
        window.setInterval(() => {
          this.updateLessons(this.lessons)
          this.autoSelectInClass()
        }, 1000)
      }
    },
    selectId() {
      this.$nextTick(() => {
        if (this.selectItem) {
          const { offsetTop } = window.document.querySelector('.video-container')
          window.scrollTo(0, offsetTop)
        }
      })
    },
  },

}
</script>

<style lang="less" scoped>
.course-detail {
  min-height: ~"calc(100vh - 90px)";
  .course-name {
    margin-bottom: 15px;
    font-size: 22px;
    display: block;
  }
  .video-container {
    /deep/ .el-card__header {
      font-size: 18px;
    }
    video {
      width: 80%;
      position: relative;
      left: 50%;
      transform: translateX(-50%);
    }
    .describe {
      width: 80%;
      display: flex;
      justify-content: space-between;
      padding-bottom: 20px;
      margin: 0 auto;
    }
  }
  .course-list {
    padding: 20px 0;
    .course-list-header {
      display: flex;
      justify-content: space-between;
      strong {
        font-size: 20px;
      }
    }
    .list {
      display: flex;
      flex-wrap: wrap;
      > div {
        width: 25%;
        padding: 10px;
        cursor: pointer;
        &:nth-child(4n + 1) {
          padding-left: 0;
        }
        &:nth-child(4n + 4) {
          padding-right: 0;
        }
        /deep/ .el-card__body {
          padding: 15px;
          display: flex;
          .head {
            height: 100px;
            width: 100px;
            flex-shrink: 0;
            margin-right: 10px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 18px;
            background-color: #eee;
            font-weight: bold;
            + div {
              width: 100%;
              display: flex;
              flex-direction: column;
              justify-content: space-between;
              .title {
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;
              }
              .time {
                color: #888;
                font-size: 14px;
                + div {
                  display: flex;
                  justify-content: space-between;
                  align-items: center;
                  .teacher {
                    font-size: 14px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>




