<template>
  <div class="header">
    <div>
      <div @click="toHome" class="logo">
        <img :src="require('assets/img/brand-logo.png')" />
        <span class="title">沃课名校课程</span>
      </div>
      <div>
        <span>{{username}}</span>
        <span class="logout" @click="logout">注销</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: '',
    }
  },
  mounted() {
    this.$api.getStudentInfo().then(({ data: { username } }) => {
      this.username = username
    })
  },
  methods: {
    toHome() {
      this.$router.push('/list')
    },
    logout() {
      window.localStorage.removeItem('accessToken')
      this.$router.push('/login')
    },
  },
}
</script>


<style lang="less" scoped>
@import "~assets/css/base-variables.less";

.header {
  width: 100%;
  height: 50px;
  background: @color-primary;
  color: #fff;
  padding: 0 10px;
  > div {
    height: inherit;
    max-width: 1500px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    .logo {
      cursor: pointer;
      img {
        width: 80px;
        margin-right: 15px;
      }
      .title {
        font-size: 18px;
      }
    }

    > div {
      display: flex;
      align-items: center;
      &:last-child {
        span {
          margin-left: 20px;
        }
        .logout {
          cursor: pointer;
          &:hover {
            color: #eee;
          }
        }
      }
    }
  }
}
</style>




