import Vue from 'vue'
import VueRouter from 'vue-router'
import dayjs from 'dayjs'

import { Form, FormItem, Input, Button, Card, Message, Loading, Select, Option } from 'element-ui'

import Login from './login'
import List from './list'
import Detail from './detail'
import VHeader from './header'

import './index.less'

import Api from './api'

Vue.use(VueRouter)

Vue.use(Form)
Vue.use(Input)
Vue.use(FormItem)
Vue.use(Button)
Vue.use(Card)
Vue.use(Select)
Vue.use(Option)
Vue.use(Loading.directive)

Vue.prototype.$message = Message
Vue.prototype.$api = new Api()

const router = new VueRouter({
  mode: 'history',
  base: process.env.NODE_ENV === 'production' ? '/pad/woke-course' : '/static/w/pages/woke-course.html',
  routes: [
    { path: '/login', component: Login },
    { path: '/list', component: List },
    { path: '/detail', component: Detail },
    { path: '/', redirect: '/list' },
  ],
  linkActiveClass: 'active',
})

const refreshToken = (token, next) => {
  new Api().userLogin({
    refresh_token: token,
    grant_type: 'refresh_token',
  }).then(({ data }) => {
    window.localStorage.setItem('accessToken', JSON.stringify({ ...data, login_time: new Date() }))
  }).catch(() => {
    next('/login')
  })
}

router.beforeEach((to, from, next) => {
  const { accessToken = '{}' } = window.localStorage
  const token = JSON.parse(accessToken)
  if (to.path !== '/login') {
    // 未登录
    if (!token.expires_in) return next('/login')
    // token过期
    const overdue = !dayjs().isBefore(dayjs(token.login_time).add(token.expires_in, 'second'))
    if (overdue) refreshToken(token.refresh_token, next)
  }
  next()
});


(async () => {
  if (process.env.NODE_ENV === 'development') await import(/* webpackChunkName: "mock" */ '@/mock')
  new Vue({
    delimiters: ['${', '}'],
    el: '#app',
    router,
    data: {},
    components: { VHeader },
  })
})()



